___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  box-sizing: border-box;
}

header {
  display: flex;
  position: relative;

  padding: 10px 30px;

  .logo {
    color: blue;
  }

  .instruction {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);

    padding: 10px;
    margin: 5px;
    background-color: blue;
    color: white;
    font-weight: bold;
  }
}

main {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  gap: 60px;
  min-height: 80dvh;
  padding-top: 30px;

  .board {
    position: relative;

    .board-label {
      text-align: center;
      margin-top: 10px;
    }

    .control-panel {
      display: flex;
      justify-content: center;
      gap: 5px;
      margin-top: 10px;
      position: absolute;
      bottom: -40px;
      left: 50%;
      transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      -moz-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
      -o-transform: translateX(-50%);

      * {
        flex: none;
      }
    }
  }

  &.game-start .start-button {
    display: none;
  }

  &.game-start .randomise-button {
    display: none;
  }

  .reset-button {
    display: none;
  }

  &.game-start .reset-button {
    display: block;
  }

  &.game-start #computer-board .board-content:hover {
    cursor: crosshair;
  }
  &.game-over #computer-board .board-content:hover {
    cursor: auto;
  }

  table {
    border-collapse: collapse;

    .board-mark-row,
    .board-mark-col {
      text-align: center;
    }

    .board-mark-row {
      padding-bottom: 5px;
    }

    .board-mark-col {
      padding-right: 5px;
    }

    .board-cell {
      border: 1px solid #000;
      padding: 0;
    }

    .board-content {
      width: 2em;
      height: 2em;

      &.hit {
        background-color: black;
      }
    }

    .board-content.occupied {
      background-color: blue;

      &.hit {
        background-color: red;
      }
    }
  }
}
